import React from "react";
import style from "./CurrentCharacterPage.module.scss"
import {useParams} from "react-router-dom";
import {useQuery} from "react-query";
import {ICharacter} from "../../types/characters.types";
import {charactersApi} from "../../api/characters.api";
import {ErrorBlock} from "../X_common/ErrorBlock/ErrorBlock";
import {Title} from "../X_common/Title/Title";
import Divider from "@mui/material/Divider";
import {PropertyRow} from "./PropertyRow/PropertyRow";

export const CurrentCharacterPage = () => {
    const {id} = useParams();

    const {
        isLoading,
        isError,
        error,
        data: character,
    } = useQuery<ICharacter, any>(
        ["character", id],
        () => charactersApi.getById(id as string),
    );


    // character && [
    //     {label: '', value: character.race.name, to: `/race/${character.race.id}`},
    //     {label: '', value: character.male },
    //     {label: '', value: character.guild.name, to: `/guild/${character.guild.id}`},
    //     {label: '', value: character?.role },
    //     {label: '', value: character.status },
    //     {label: '', value: character.location.name, to: `/location/${character.location.id}`},
    //     {label: '', value: character?.sublocation?.name, to: `/location/${character.sublocation.id}`},
    //     {label: '', value: character.religion.name, to: `/religion/${character.religion.id}`},
    //     {label: 'Code', value: character?.code },
    // ]


    return (
        <div className={style.currentCharacterPage}>
            {
                isError ? (
                    <ErrorBlock error={error}/>
                ) : (
                    <>
                        {
                            character && (
                                <>
                                    <Title title={character.name} className={style.title}/>
                                    <div className={style.content}>
                                        <div className={style.descriptionBlock}>
                                            <p className={style.label}></p>
                                            <Divider className={style.divider}/>
                                            <p className={style.description}>{character.description}</p>
                                        </div>
                                        <div className={style.cardBlock}>
                                            <p className={style.name}>{character.name}</p>
                                            <p className={style.description}>{character.description}</p>
                                            <img src={character.image.large} alt=""/>
                                            <div className={style.properties}>
                                                {
                                                    [
                                                        {label: '', value: character.race.name, to: `/race/${character.race.id}`},
                                                        {label: '', value: character.male },
                                                        {label: '', value: character.guild.name, to: `/guild/${character.guild.id}`},
                                                        {label: '', value: character?.role },
                                                        {label: '', value: character.status },
                                                        {label: '', value: character.location.name, to: `/location/${character.location.id}`},
                                                        {label: '', value: character?.sublocation?.name, to: `/location/${character?.sublocation?.id}`},
                                                        {label: '', value: character?.religion?.name, to: `/religion/${character?.religion?.id}`},
                                                        {label: 'Code', value: character?.code },
                                                    ]
                                                        .map(({label, value, to}, index) => (
                                                            value ? <PropertyRow key={index} label={label} value={value} to={to}/> : null
                                                        ))
                                                }
                                            </div>
                                        </div>
                                    </div>
                                </>
                            )

                        }
                    </>
                )
            }
        </div>
    )
}